import BestCase from "components/base/bestcase/BestCase";
import Brand from "components/base/brand/Brand";
import Introduce from "components/base/introduce/Introduce";
import OurGame from "components/base/ourgame/OurGame";
import ScrollVideo from "components/base/scrollVideo/ScrollVideo";
import Strength from "components/base/strength/Strength";
import Value from "components/base/value/Value";
import React from "react";
import { useLocalizePage } from "store/actions/localizeAction";
import "./WhoWeAre.scss";
import { Helmet } from "react-helmet";

const WhoWeAre: React.FC = () => {
  useLocalizePage("whoweare", "/api/page/en/who-we-are");
  return (
    <div className="com-whoweare">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Who We Are | Gameloft</title>
        <meta name="description" content="Discover the world of gaming and development. We create unique gamified experiences for brands and incomparable connections to players." />
        <link rel="canonical" href="https://www.gameloft.com/forbrands/who-we-are" />
      </Helmet>
      <Introduce scrollable="true" />
      <Value></Value>
      <ScrollVideo />
      <Strength showArrow={false} keyIndex={null} />
      <BestCase />
      <Brand />
      <OurGame />
    </div>
  );
};

export default WhoWeAre;
